* {
    margin: 0;
    padding: 0;
}

.contact {
    /* width: 100vw; */
    background-color: rgb(232, 232, 232);
}

.contact__wrapper {
    width: 100%;
    display: flex;
    justify-content: center;
    max-width: 980px;
    margin: 0 auto;
}



.contact__div {
    flex-basis: 25%;
}



.contact__div {
    display: flex;
    justify-content: center;
    align-items: center;
}

.contact__logo {
    flex-grow: 0;
    flex-basis: 20%;
    justify-content: flex-start;
}

.contact__header {
    font-weight: normal;
}

.contact__paragraph {
    font-weight: bold;
}

.contact__div--left {
    text-align: right;
    display: flex;
    justify-content: flex-end;
    padding-right: 30px;
}

.contact__div--right {
    text-align: left;
    display: flex;
    justify-content: flex-start;
    /* padding-left: 30px; */
}

.contact__icon {
    font-size: 30px;
    border: 3px solid orange;
    padding: 5px;
    width: 30px;
    height: 30px;
    display: flex;
    justify-content: center;
    align-items: center;
    /* margin-left: 10px; */
}

.contact__div .contact__icon {
    margin-left: 0;
    margin-right: 10px;
}

.contact__clock {
    color: orange;

}

@media (max-width: 900px) {
    .contact {
        padding: 10px;
        overflow-x: hidden;
    }

    .contact__div {
        padding: 10px;
        justify-content: center;
    }

    .contact__logo {
        display: none;
    }

    .contact__div {
        flex-basis: 30%;
    }
}

@media (max-width: 768px) {
    .contact__header {
        display: none;
    }

    .contact {
        padding: 0;
    }
}

@media (max-width: 576px) {
    .contact__wrapper {
        flex-wrap: wrap;
    }

    .contact__div {
        flex-basis: 40%;
        font-size: 1.3em;
    }

    .contact__div:last-child {
        flex-basis: 50%;
    }
}